import Axios from 'axios';
import API from 'config/api';

export const newPackage = (data) => API.post('/', data);

export const updatePackage = (data) => API.post('/packages', data);

export const removePackage = (id) => API.delete(`/packages/${id}`);

export const savePosts = ({ id, name, description, posts }) =>
  API.post(`/packages`, { id, name, description, posts });

export const uploadImage = ({ url, img }) => {
  return fetch(url, {
        method: 'PUT',
        body: img,
        headers: {
          'Content-Type': 'application/octet-stream',
        }
    }).then(response => response.json())
}

export const removeImage = ({ url, img }) => Axios.delete(url, img);

export const publishPackage = (id) => API.post(`/packages/publish/${id}`);
